import React, { useEffect } from "react";
import PageHeader from ".././header";
import ".././style.css";
import styles from "./our-story.module.css";
import Timeline from "./our-story-timeline.js";

// Start at the top of the page
const OurStory = () => {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div>
      {/*Cover Photo*/}
      <PageHeader imgUrl="/headers/our-story-header.jpg" title="Our Story" />
      {/*Start of Our Story*/}
      {/*Start of Our Mission*/}
      <h2 className={styles.oppSubheading}>Our Mission</h2>
      <div className={styles.ourMissionSection}>
        <div className={styles.ourMissionImg}>
          <img
            src={process.env.PUBLIC_URL + "/our-story/our-mission.jpg"}
            alt="WIT members"
          />
          <br />
        </div>
        <div className={styles.ourStory}>
          <p className={styles.ourMissionContent}>
            Formed in 2016, UNSW Women in Technology (WIT) is currently a
            network of over 2800 students that aims to collaborate, empower, and
            up-skill female and males students within STEM to encourage
            diversity within the technology industry.
          </p>
          <p className={styles.ourMissionContent}>
            At WIT, we are proud of being able to provide opportunities that
            develop all students' professional and technical skills. Whether
            it's personal, academic, or social, we want our members to become
            the best version of themselves.
          </p>
          <p className={styles.ourMissionContent}>
            Our society captures a diverse range of disciplines, enabling us to
            inspire students from various backgrounds by providing them with a
            variety of events and multimedia resources such as blogs, podcasts,
            videos, guides to succeeding professionally and within uni,
            hackathons, course revision workshops, professional development
            workshops, and so much more!
          </p>
        </div>
      </div>
      {/*Start of Our Mascot*/}
      <h2 className={styles.oppSubheading}>Our Mascot</h2>
      <div className={styles.ourMascotSection}>
        <div className={styles.ourMascotImg}>
          <img
            src={process.env.PUBLIC_URL + "/our-story/our-mascot.png"}
            alt="Introducing Willow the Australian Wombat, WIT's mascot"
          />
          <br />
        </div>
        <div className={styles.ourStory}>
          <p className={styles.ourMascotContent}>
            Have you met Willow? Willow is WIT's trusty mascot and biggest
            supporter. Willow, like us, shares a dislike of bugs (the crawley
            kind and the codey kind) and believes in empowering all creatures.
            Willow is only four years old but she understands the power of
            nurturing young creatures and showing them how to navigate the
            forest. When shes not burrowed under the desk having a quick
            snooze, she can be found munching on a shrub salad and cheering on
            the WIT team in everything they do.
          </p>
          <p className={styles.ourMascotContent}>
            Willow debuted as WITs mascot in 2020, and has been bringing
            laughter and motivation to our community ever since! In 2021, Willow
            was brought to life from the screen to O-Week as a handmade
            crocheted plushie. Through a&nbsp;
            <a
              href="https://www.facebook.com/unsw.wit/posts/2510541182584081"
              className={styles.link}
              target="_blank"
              rel="noopener noreferrer"
            >
              cute live-action photo shoot
            </a>
            , styled with her favourite orange flower, Willow recreated iconic
            WIT memes and embodied every stage of a student in tech - from
            fuelling up on coffee to taking power naps during study sessions.
          </p>
        </div>
      </div>
      {/*End of Our Mascot*/}
      {/*Start of Our History*/}
      <h2 className={styles.oppSubheading}>Our History</h2>
      <div className={styles.ourHistorySection}>
        <div className={styles.ourStory}>
          <p className={styles.ourHistoryContent}>
            Women in Technology was founded by UNSW STEM students, Silvia Lin
            and her co-founders Rachel Lin, Emily Chen, Anushka Dutt, Chelsea
            Leung, Karen Huang and Kris Mansfield.
          </p>
          <p className={styles.ourHistoryContent}>
            The vision which sparked WIT was to empower and unite female
            students studying technology disciplines by providing a platform to
            share experiences and knowledge. They were passionate about wanting
            to ensure that young women were better informed about the
            opportunities within technology and had a network to support them.
            The teams goals were centralised around breaking the stereotypes of
            the tech industry and forming a close-knit community where they
            could provide support for each other, both in pursuing a career in
            tech and developing themselves as individuals.
          </p>
          <p className={styles.ourHistoryContent}>
            Today, WIT continues to uphold their vision and grow with many more
            upcoming initiatives to come.
          </p>
        </div>
      </div>
      {/*Start of timeline*/}
      <Timeline />
      <p className={styles.ourHistoryContent} />
      <p className={styles.ourHistoryContent} />
      {/*End of timeline*/}
      {/*End of Our History*/}
    </div>
  );
};

export default OurStory;
